.pcard {
    font-size: medium;
    width: 65%;
    background-color: var(--secondary);
    padding: 2%;
    border-radius: 10px;
    border-color: var(--text);
    border-style: solid;
    border-width: 1px;
    margin-bottom: 4%;

    margin-left: 1%;
    margin-top: 1%;

    will-change: filter;

    transition: filter .3s ease-out;
}

.pcard:hover{
    filter: drop-shadow(0px 0px 4px var(--text));
}

.pname {
    font-size: 3rem;
}

.pdesc {
    font-size: 26px;
    line-height: 1.3;
}

.pheading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 3%;
}

.pimage {
    object-fit: cover;
    max-width: 10%;
    width: auto;
    height: auto;
    border-radius: 10px;
    aspect-ratio: initial;
}

.pcontainer{
/*    background-color: white;*/
    overflow: scroll;
    height: 80vh;
}

.pintro{
    font-size: 1.5rem;
    margin-bottom: 2vh;
}

@media only screen and (max-width: 900px) {
    .pcard{
        width: 82vw;
        
    }
    .pheading{
        flex-direction: row;
    }
    .pname{
        font-size: large;
    }
    .pdesc{
        font-size: medium;
    }
  }
