@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');

.headerContainer {
    grid-area: 1 / 1 / 2 / 3;
    background-color: var(--secondary);
    color: var(--text);
    font-size: 1.7rem;

    display: flex;
    flex-direction: row;
    justify-content: space-around;

    border: 1px;
    border-style: none none solid none;
    border-color: var(--text);
}

.pageName {
    font-family: 'Inter', sans-serif;
    font-size: 1.1rem;
    height: 100%;
    width: 0vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.headerButtons {
    display: flex;
    gap: 30%;
}

.themeButton {
    all: unset;
    cursor: pointer;
}


.githubLogo{
    fill: var(--text);
}