@import url('https://fonts.googleapis.com/css2?family=Signika+Negative:wght@300;700&display=swap');

:root {
  --text: #ffffff;
  --background: #191919;
  --primary: 64, 89, 255;
  --secondary: #212121;
  --accent: #ebff38;

  color: var(--text);
}


.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}



.transition {
  transition: background-color 150ms linear;
  -webkit-transition: background-color 150ms linear;
}

.contents {
  
  padding: 7vh 0 0 12vw;
  grid-area: 2 / 2 / 6 / 3;
}

.textContainer {
  color: var(--text);
  font-family: 'Signika Negative', sans-serif;
  font-size: max(4vh, 4vw);
}

.bigName {
  font-weight: 600;
  font-size: max(6vh, 4vw);
}

.backgroundContainer {
  background-color: var(--background);
  height: 100vh;
  width: 100vw;

  display: grid;
  grid-template-columns: 1fr 7fr;
  grid-template-rows: 1fr 15fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.linksContainer {
  display: flex;
  gap: 1.7rem;
}


.linkButton {
  all: unset;
  color: var(--text);
  background-color: var(--primary);
  padding: 10px;
  border-radius: 10px;

  font-size: max(1.4vh, 1.4vw);
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

}

.linkButton:hover {
  box-shadow: 0px 10px 100px var(--primary);
  transform: translateY(-2px);

}

.introText{
  font-size: max(3vh, 1.5vw);;
}

.mobProj{
  visibility: hidden;
}

.mobpbutton{
  all: unset;
  text-decoration: underline;
  cursor: auto;
}

@media only screen and (max-width: 900px) {
  .mobProj {
    visibility: visible;
  }
  .mobpbutton{
    all: unset;
    text-decoration: underline;
    cursor: pointer;
  }
  .contents{
    width: 100%;
    padding: 1vh;
    padding-top: 3vh;
    margin-left: -10%;
  }
}
